import Vuex from 'vuex';
import Vue from 'vue';
Vue.use(Vuex);
// /**
//  * 状態を保持したい変数の管理
//  */
var state = {
    count: 0,
    windowWidth: 0,
    isMobile: false
};
/**
 * actionsはmutationsを利用して，アクションの処理を実装
 */
var actions = {
    increment: function (context) {
        context.commit('plus');
    },
    decrement: function (_a) {
        var commit = _a.commit;
        commit('sub');
    },
    resizeWindowWidth: function (context) {
        context.commit('windowWidth', document.documentElement.clientWidth);
        context.commit('isMobile');
    }
};
/**
 * gettersはstateの値を取得するのに利用
 */
var getters = {
    getCount: function (state) {
        return state.count;
    },
    getWindowWidth: function (state) {
        return state.windowWidth;
    },
    getIsMobile: function (state) {
        return state.isMobile;
    }
};
/**
 * mutationsは値の移り変わりの処理を実装
 */
var mutations = {
    plus: function (state) {
        state.count += 1;
    },
    sub: function (state) {
        state.count -= 1;
    },
    windowWidth: function (state, _windowWidth) {
        state.windowWidth = _windowWidth;
    },
    isMobile: function (state) {
        if (state.windowWidth < 1264) {
            state.isMobile = true;
        }
        else {
            state.isMobile = false;
        }
        console.log(state.isMobile);
    }
};
export default new Vuex.Store({
    state: state,
    actions: actions,
    getters: getters,
    mutations: mutations
});



